<template>
  <div class="box">
    <Header>
      <div slot="center">地址列表</div>
    </Header>
    <div class="content">
      <van-address-list
        v-model="chosenAddressId"
        :list="list"
        @add="onAdd"
        @edit="onEdit"
        @select="onSelect"
      />
    </div>
  </div>
</template>
<style lang="scss" scoped>
@import '@/lib/reset.scss';
.van-address-item__name {
  font-size: 15px;
}
</style>
<script>
import Header from '@/components/Header'
import axios from '@/utils/request'
import Vue from 'vue'
import { AddressList, Dialog, Toast } from 'vant'
Vue.use(AddressList)
Vue.use(Dialog)
Vue.use(Toast)
export default {
  components: {
    Header
  },
  data () {
    return {
      chosenAddressId: 1,
      list: [],
      flag: false,
      userid: ''
    }
  },
  created () {
    let userid = localStorage.getItem('userid')
    let token = localStorage.getItem('token')
    console.log(token)
    if (!token) {
      Dialog.alert({
        message: '您还没有登录，请前往登录'
      })
      this.$router.replace('/login')
    } else {
      let url = '/address?userid=' + userid
      axios.get(url).then(res => {
        console.log(res.data)
        if (res.data.code !== '30000') {
          for (let i = 0; i < res.data.data.length; i++) {
            res.data.data[i].id = i
            res.data.data[i].address = res.data.data[i].province + res.data.data[i].city + res.data.data[i].county + res.data.data[i].addressDetail
            if (res.data.data[i].flag === 1) {
              this.chosenAddressId = i
            }
          }
          console.log(res.data.data)
          this.list = res.data.data
        } else {
          Dialog.alert({
            message: '您还没有收货地址，请添加'
          })
        }
      })
    }
  },
  methods: {
    onAdd () {
      // Toast('新增地址')
      this.$router.push('/addaddress')
    },
    onEdit (item, index) {
      // Toast('编辑地址:' + index)
      // console.log(item)
      this.$router.push('/updateaddress?addressid=' + item.addressid)
    },
    onSelect (item, index) {
      console.log(index, item.userid)
      axios.post('/address/updatedefault', {
        userid: item.userid,
        addressid: item.addressid
      }).then(res => {
        console.log('应该成功了啊')
        // Toast('已修改默认地址')
      })
    },
    goback () {
      this.$router.back()
    }
  }
}
</script>
